.last--workouts--wrapper {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
  padding: 1.5rem;
  border: 2px solid #ccc;
  border-radius: 10px;

  background-image: radial-gradient(
      circle at center center,
      rgba(217, 217, 217, 0.05) 0%,
      rgba(217, 217, 217, 0.05) 15%,
      rgba(197, 197, 197, 0.05) 15%,
      rgba(197, 197, 197, 0.05) 34%,
      rgba(178, 178, 178, 0.05) 34%,
      rgba(178, 178, 178, 0.05) 51%,
      rgba(237, 237, 237, 0.05) 51%,
      rgba(237, 237, 237, 0.05) 75%,
      rgba(138, 138, 138, 0.05) 75%,
      rgba(138, 138, 138, 0.05) 89%,
      rgba(158, 158, 158, 0.05) 89%,
      rgba(158, 158, 158, 0.05) 100%
    ),
    radial-gradient(
      circle at center center,
      rgb(255, 255, 255) 0%,
      rgb(255, 255, 255) 6%,
      rgb(255, 255, 255) 6%,
      rgb(255, 255, 255) 12%,
      rgb(255, 255, 255) 12%,
      rgb(255, 255, 255) 31%,
      rgb(255, 255, 255) 31%,
      rgb(255, 255, 255) 92%,
      rgb(255, 255, 255) 92%,
      rgb(255, 255, 255) 97%,
      rgb(255, 255, 255) 97%,
      rgb(255, 255, 255) 100%
    );
  background-size: 42px 42px;
}

.last--workouts--title {
  font-weight: bold;
  color: #555;
  text-shadow: 2px 2px 5px purple;
  text-align: center;
  font-size: x-large;
  margin-bottom: 1.5rem;
}

.last--workouts--sportsman {
  text-shadow: 2px 2px 5px gold;
  color: #333;
  font-size: large;
}

.profile--img {
  display: block;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 3px solid #ccc;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.5rem;
  margin-bottom: 2rem;
}

.add--workout--btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  width: fit-content;
  height: 40px;
  padding: 5px 15px;
  border: 2px solid #ccc;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  font-size: medium;
}

.add--workout--btn:hover {
  border: 2px solid #aaa;
  background-color: #ddd;
}
